// colors

// Primary
$primary-color: hsl(163, 72%, 41%);
$secondary-color: hsl(356, 69%, 56%);

// social-networks
$facebook-color: hsl(195, 100%, 50%);
$twitter-color: hsl(203, 89%, 53%);

$instagram-color: linear-gradient(45deg, rgb(253,196,104), rgb(223,73,150));
// $instagram-color: linear-gradient(hsl(37, 97%, 70%) to hsl(329, 70%, 58%));
$youtube-color: hsl(348, 97%, 39%);

// toggle
$primary-toggle: linear-gradient(hsl(210, 78%, 56%) to hsl(146, 68%, 55%));
$secondary-toggle: hsl(230, 22%, 74%);

// Dark-theme
$dark-background-blue: hsl(230, 17%, 14%);
$dark-top-pattern-blue: hsl(232, 19%, 15%);
$dark-background-card-blue: hsl(228, 28%, 20%);
$dark-color-text-blue: hsl(228, 34%, 66%);
$dark-color-text-white: hsl(0, 0%, 100%);

// light-theme
$light-background-white:hsl(0, 0%, 100%);
$light-top-pattern-pale-blue:hsl(225, 100%, 98%);
$light-background-card:hsl(227, 47%, 96%);
$light-color-text-grayish-blue:hsl(228, 12%, 44%);
$light-color-text-dark-blue:hsl(230, 17%, 14%);

// Typography

$font-family:'Inter', sans-serif;
$font-weight-normal: 400;
$font-weight-bold: 700;
$font-size: 14px;

// Breakpoints
$m: 375px;
$l: 1200px;

// other

$gutter: 1em;
$border-radius: $gutter / 4;